/* Animation */

@-webkit-keyframes wiggle-hand {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(15deg);
  }
}

@keyframes wiggle-hand {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(15deg);
  }
}

@-webkit-keyframes wiggle-papper {
  0% {
    transform: rotate(5deg);
  }

  100% {
    transform: rotate(10deg);
  }
}

@keyframes wiggle-papper {
  0% {
    transform: rotate(5deg);
  }

  100% {
    transform: rotate(10deg);
  }
}

@-webkit-keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes flicker {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.85;
  }
}

@keyframes flicker {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.85;
  }
}

@-webkit-keyframes scale-music {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.1);
  }
}

@keyframes scale-music {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.1);
  }
}

@-webkit-keyframes move-eye {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(-2px);
  }
}

