/* Links */

.link-secondary {
  display: inline-block;
  font-size: calc(14px + 2 * ((100vw - 1024px) / 416));
  font-weight: 300;
  color: var(--color-blue);
  text-decoration: none;
  transition: color ease-out 0.15s, fill ease-out 0.15s;
  border: 1px solid transparent;
  padding: 4px;
  border-radius: 5px;
}

@media screen and (min-width: 1440px) {

  .link-secondary {
    font-size: 16px;
  }
}

@media screen and (max-width: 1024px) {

  .link-secondary {
    font-size: 14px;
  }
}

.link-secondary__arrow {
  fill: var(--color-blue);
  transition: fill ease-out 0.15s;
}

.link-secondary:hover,
.link-secondary:focus,
.link-secondary:active {
  color: var(--color-white);
}

.link-secondary:target,
.link-secondary:focus {
  border: 1px solid var(--color-blue);
}

.link-secondary:disabled {
  opacity: 0.45;
}

.link-secondary:hover .link-secondary__arrow,
.link-secondary:focus .link-secondary__arrow,
.link-secondary:active {
  fill: var(--color-white);
}

