/* Buttons */

.button {
  font-family: var(--font-helvetica-neue-cyr);
  font-weight: 500;
  position: relative;
  cursor: pointer;
  display: inline-block;
  font-style: normal;
  text-decoration: none;
  text-align: center;
  font-size: 16px;
  line-height: 16px;
  border-radius: 5px;
  padding: 15px 35px;
  max-width: 232px;
  z-index: 10;
  box-shadow: 10px 10px 20px -10px rgba(0, 0, 0, 0.15);
  outline: var(--color-blue);
  transition: background 0.25s ease-in-out, color 0.25s ease-in-out, border 0.25s ease-in-out;
  overflow: hidden;
}

.button--small {
  padding: 8px 15px;
  font-weight: 300;
}

.button--blue {
  border: 1px solid transparent;
}

.button--blue {
  color: var(--color-white);
  transition: color 0.3s, border 0.3s, background 0.3s;
}

.button--dark {
  border: 1px solid transparent;
  color: var(--color-light-gray);
  background: var(--color-dark-gray);
  opacity: 0.55;
  transition: color 0.3s, background 0.3s, opacity 0.3s;
}

.button--dark:hover,
.button--dark:focus,
.button--dark:target,
.button--blue:active {
  opacity: 1;
  color: var(--color-blue);
}

.button--dark.is-active {
  opacity: 1;
  color: var(--color-white);
  background: var(--color-blue);
}

.button--blue::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background: var(--color-blue);
  z-index: -1;
  transition: width 0.25s;
}

.button--blue:hover::before,
.button--blue:focus::before,
.button--blue:target::before {
  width: 0%;
}

.button--blue:hover,
.button--blue:focus,
.button--blue:target {
  color: var(--color-blue);
  background: var(--color-white);
  border: 1px solid var(--color-white);
}

.button--blue:active {
  color: var(--color-white);
  background: var(--color-white);
}

.button--blue:disabled {
  opacity: 0.45;
}

